/**
* style for checkboxes
*/
/* basic checkbox */
.rpgui-content input[type=checkbox].rpgui-checkbox {
  display: none; }

.rpgui-content input[type=checkbox].rpgui-checkbox + label {
  background: url(/static/media/checkbox-off.b84a6f73.png) no-repeat;
  line-height: 24px;
  display: inline-block;
  background-size: auto 100%;
  padding-left: 34px;
  height: 24px;
  margin-top: 10px;
  margin-bottom: 10px; }

.rpgui-content input[type=checkbox].rpgui-checkbox:checked + label {
  background: url(/static/media/checkbox-on.ee46ca08.png) no-repeat;
  line-height: 24px;
  display: inline-block;
  background-size: auto 100%;
  padding-left: 34px;
  height: 24px; }

/* golden checkbox */
.rpgui-content input[type=checkbox].rpgui-checkbox.golden + label {
  background: url(/static/media/checkbox-golden-off.64d7ce7e.png) no-repeat;
  background-size: auto 100%; }

.rpgui-content input[type=checkbox].rpgui-checkbox.golden:checked + label {
  background: url(/static/media/checkbox-golden-on.b6e46084.png) no-repeat;
  background-size: auto 100%; }

/**
* global content styling
*/
/* game div with background image*/
.rpgui-content {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  position: fixed;
  overflow: hidden;
  font-size: 0.8em; }

/* general rules to apply on anything inside the content */
.rpgui-content * {
  /* remove outline effect for input elements etc */
  outline: none;
  /* prevent dragging */
  user-drag: none;
  -webkit-user-drag: none;
  /* prevent text selecting */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  /* pixelated enlargement filter (to keep the pixel-art style when enlarging pictures) */
  image-rendering: -webkit-crisp-edges;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: pixelated;
  /* default font */
  font-family: 'Press Start 2P', cursive; }

